<template>
  <div>
    <div class="search">
      <input class="search-input" v-model="keyword" type="text" placeholder="请输入城市名或拼音"/>
    </div>
    <div class="search-content" ref="search" v-show="keyword">
      <ul>
        <router-link to="/">
        <li  class="search-item border-bottom"
             v-for="item of list"
             @click="handleCity(item.name)"
        >{{item.name}}</li>
        </router-link>
      </ul>
    </div>
  </div>
</template>
<script>
import Bscroll from 'better-scroll'
export default {
  name:'citySearch',
  props:{
    cities:Object
  },
  data (){
    return{
      keyword:'',
      list:[]
    }
  },
  methods:{
    handleCity (city){
      this.$store.commit('changeCity',city)
    }
  },
  mounted(){
    this.scroll=new Bscroll(this.$refs.search)
  },
  watch:{
    keyword (){
      if(this.timer){
        clearTimeout(this.timer)
      }
      if(!this.keyword){
        this.list=[]
        return
      }
      this.timer=setTimeout(()=>{
        const result=[]
        for(let i in this.cities){
          this.cities[i].forEach((value) =>{
            if(value.spell.indexOf(this.keyword)>-1||
            value.name.indexOf(this.keyword) >-1){
              result.push(value)
            }
          })
        }
        this.list=result
    },100)
    }
  }
}
</script>
<style lang="stylus" scoped>
  @import '~styles/varibles'
  .search
      height:.72rem
      background:$bgColor
      padding:0 .1rem
    .search-input
      height:.62rem
      width:100%
      line-height:.62rem
      text-align:center
      border-radius:.06rem
      color:#666
  .search-content
     z-index:1
     overflow:hidden
     position:absolute
     top:1.58rem
     left:0
     right:0
     bottom:0
     background:#eee
    .search-item
      font-size:.4rem
      color:#666
      padding:.2rem
      background:#fff
</style>



