<template>
  <div>
    <home-header ></home-header>
    <home-swiper :swiperList="swiperList"></home-swiper>
    <home-icons :iconsList="iconsList"></home-icons>
    <home-recommend :recommendList="recommendList"></home-recommend>
  </div>
</template>
<script>
import homeHeader from './compenents/header'
import homeSwiper from './compenents/swiper'
import homeIcons from './compenents/icons'
import homeRecommend from './compenents/recommend'
import axios from 'axios'
import { mapState } from 'Vuex'

export default {
  name: 'home',
  data(){
    return{
      lastCity:'',
      swiperList:[],
      iconsList:[],
      recommendList:[]
    }
  },
  components: {
    homeHeader,
    homeSwiper,
    homeIcons,
    homeRecommend
  },
  methods: {
    getHomeInfo() {
      axios.get('/static/mock/index.json?city=' + this.$store.state.city)
        .then(this.getHomeInfoSucc)
    },
    getHomeInfoSucc(res) {
      res=res.data
      if(res.ret&&res.data){
        const data=res.data
        this.city=data.city,
          this.swiperList=data.swiperList,
          this.iconsList=data.iconsList,
          this.recommendList=data.recommendList
      }
    }
  },
  mounted() {
    this.lastCity=this.$store.state.city
    this.getHomeInfo()
  },
  activated (){
    if(this.lastCity !== this.$store.state.city){
      this.lastCity=this.$store.state.city
      this.getHomeInfo()

    }
  }
}
</script>
<style>

</style>



