<template>
    <div class="list" ref="wrapper">
      <div>
      <div class="area">
        <div class="title border-topbottom">当前城市</div>
        <div class="button-list">
          <div class="button-wraper">
            <router-link to="/">
            <div class="button">{{this.$store.state.city}}</div>
            </router-link>
          </div>
        </div>
      </div>
      <div class="area">
        <div class="title border-topbottom">热门城市</div>
        <div class="button-list">
          <div class="button-wraper"
               v-for="item of hotCities"
               :key="item.id"
               @click="handleCity(item.name)"
          >
            <div class="button">{{item.name}}</div>
          </div>
        </div>
      </div>
      <div class="area"
           v-for="(item,key) of cities"
           :key="key"
           :ref="key"
      >
        <div class="title border-topbottom">{{key}}</div>
        <div class="item-list"
             v-for="innerItem of item"
             :key="item.id"
             @click="handleCity(innerItem.name)"
        >
          <router-link to="/">
          <div class="item border-bottom">{{innerItem.name}}</div>
          </router-link>
        </div>
      </div>

      </div>
    </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
   name: 'cityList',
  props:{
    cities:Object,
    hotCities:Array
  },
  data(){
     return{
       letter:''
     }
  },
  methods:{
    handleCity (city){
      this.$store.dispatch('changeCity',city)
      this.$router.push('/')
    }
  },
  mounted(){
    this.scroll = new BScroll(this.$refs.wrapper)
    var this_=this
    this.$root.Bus.$on('change',function (msg) {
      this_.letter=msg
    })
  },
  watch:{
     letter (){
      const element = this.$refs[this.letter][0]
       this.scroll.scrollToElement(element)
     }
  }
}
</script>
<style lang="stylus" scoped>
    .border-topbottom
       &:before
         border-color:#ccc
       &:after
         border-color:#ccc
    .border-bottom
       &:before
        border-color:#ccc
    .list
       position:absolute
       top:1.58rem
       left:0
       right:0
       bottom:0
       overflow:hidden
      .title
         background:#eee
         line-height:.54rem
         padding-left:.2rem
         color:#666
      .button-list
        padding:.1rem .6rem .1rem .1rem
        overflow:hidden
        .button-wraper
           float:left
           width:33%
          .button
            padding:.1rem 0
            margin:.15rem
            text-align:center
            border:solid .02rem #ccc
            border-radius:.05rem
      .item-list
        .item
          margin-left:.2rem
          line-height:.66rem
          color:#333
          padding.1rem

</style>



