<template>
<div class="icons">
  <swiper :options="swiperOption">
    <swiper-slide v-for="(page,index) of pages" :key="index">
        <div class="icon" v-for='item of page' :key='item.id'>
            <div class="icon-img">
             <img  class="icon-img-cont" :src='item.imgUrl' >
            </div>
            <p class="icon-des">{{item.des}}</p>
          </div>
    </swiper-slide>
  </swiper>
</div>
</template>
<script>
export default {
  name: 'homeIcons',
  props:{
    iconsList:Array
  },
  data () {
    return {
      swiperOption: {
        autoplay:false
      }
    }
  },
  computed:{
    pages (){
      const pages=[]
      this.iconsList.forEach((item,index) =>{
        const page = Math.floor(index/8)
        if(!pages[page]){
          pages[page]=[]
        }
        pages[page].push(item)
      })
      return pages

    }
  }
}
</script>
<style lang="stylus" scoped>
    .icons >>> .swiper-container
          overflow:hidden
          height: 0
          padding-bottom:50%
    .icon
       position:relative
       overflow:hidden
       float:left
       width:25%
       height:0
       padding-bottom:25%
      .icon-img
           position:absolute
           top:0
           left:0
           right:0
           bottom:.44rem
           overflow:hidden
           box-sizing:border-box
           padding:.1rem
        .icon-img-cont
           width:100%
           display:block
           margin:0 auto
      .icon-des
        position:absolute
        bottom:0
        left:0
        right:0
        height:.44rem
        line-height:.44rem
        text-align:center
        color:#333
</style>



