<template>
  <ul class="list">
    <li class="item"
        v-for="(item,key) of cities"
        :key="key"
        @click="handleClick"
    >{{key}}</li>
  </ul>
</template>
<script>
    export default {
        name: 'cityAlphabet',
      props:{
       cities:Object
      },
      methods:{
        handleClick (e) {
          this.$root.Bus.$emit('change',e.target.innerText)
        }
      }
    }
</script>
<style lang="stylus" scoped>
@import '~styles/varibles.styl';
  .list
     display:flex
     flex-direction:column
     justify-content:center
     position:absolute
     top:1.58rem
     right:0
     bottom:0
     width:.4rem
     .item
       color:$bgColor
       line-height:.4rem
       text-align:center
</style>



